:root {
	--spacer-base: 1rem;
}

:root {
	--body-bg-color: #fff;
	--color: #0d0d0d;
	--link-color: #3935cd;
	--link-hover-color: #272491;
	--link-color-inversed: #74c0cd;
	--link-hover-color-inversed: #41a3b4;
	--components-background: #333;
	--components-background-hover: #1a1a1a;
	--components-color: #f2f2f2;
	--components-background-inversed: #fff;
	--components-background-hover-inversed: #e6e6e6;
	--components-color-inversed: #0d0d0d;
	--tables-border-color: #262626;
	--tables-cells-border-color: gray;
	--tables-background: #fff;
	--tables-headings-cells-background: #e6e6e6;
	--tables-zebra-headings-cells-background: #ccc;
	--tables-zebra-cells-background: #f2f2f2;
	--tables-cells-hover-background: #e6e6e6;
	--input-border-color: #ccc;
	--input-hover-focus-border-color: gray;
	--input-background: #fff;
	--input-disabled-background: #e6e6e6;
	--checkbox-active-background: #ccc;
	--checkbox-checked-background: black;
	--checkbox-checked-icon-color: #fff;
	--checkbox-disabled-border-color: #b3b3b3;
	--checkbox-disabled-background: #e6e6e6;
	--range-track-color: #4d4d4d;
	--range-track-disabled-color: #666;
	--range-thumb-color: #ccc;
	--range-thumb-color-hover: #b3b3b3;
	--progress-background: #ccc;
	--blockquotes-background: #e6e6e6;
	--blockquotes-color: #0d0d0d;
	--blockquotes-cite-color: #4d4d4d;
	--blockquotes-quote-color: #b3b3b3;
	--cards-subtitle-color: #666;
}

@media (prefers-color-scheme: dark) {
	:root {
		--body-bg-color: #262626;
		--color: #f2f2f2;
		--link-color: #74c0cd;
		--link-hover-color: #41a3b4;
		--link-color-inversed: #3935cd;
		--link-hover-color-inversed: #272491;
		--components-background: #fff;
		--components-background-hover: #e6e6e6;
		--components-color: #0d0d0d;
		--components-background-inversed: #333;
		--components-background-hover-inversed: #1a1a1a;
		--components-color-inversed: #f2f2f2;
		--tables-border-color: #4d4d4d;
		--tables-cells-border-color: gray;
		--tables-background: #333;
		--tables-headings-cells-background: #1a1a1a;
		--tables-zebra-headings-cells-background: #1a1a1a;
		--tables-zebra-cells-background: #404040;
		--tables-cells-hover-background: #262626;
		--input-border-color: gray;
		--input-hover-focus-border-color: #ccc;
		--input-background: #333;
		--input-disabled-background: #b3b3b3;
		--checkbox-active-background: #666;
		--checkbox-checked-background: #e6e6e6;
		--checkbox-checked-icon-color: #1a1a1a;
		--checkbox-disabled-border-color: #b3b3b3;
		--checkbox-disabled-background: #666;
		--range-track-color: gray;
		--range-track-disabled-color: #b3b3b3;
		--range-thumb-color: #fff;
		--range-thumb-color-hover: #e6e6e6;
		--progress-background: #ccc;
		--blockquotes-background: #333;
		--blockquotes-color: #f2f2f2;
		--blockquotes-cite-color: #999;
		--blockquotes-quote-color: #666;
		--cards-subtitle-color: #999;
	}
}

@media (prefers-reduced-motion: reduce) {
	* {
		transition: none !important;
		animation: none !important;
	}
}

*,
*::before,
*::after {
	box-sizing: border-box;
	max-width: 100%;
}

html {
	font-family: sans-serif;
	font-size: 62.5%;
	font-size: calc(1em * .625);
	line-height: 1.15;
	-webkit-text-size-adjust: 100%;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

article,
aside,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section {
	display: block;
}

@media (max-width: 575px) {
	div,
    textarea,
    table,
    td,
    th,
    code,
    pre,
    samp {
		word-wrap: break-word;
		hyphens: auto;
		overflow-wrap: break-word;
	}
}

body {
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
	font-size: 1.4rem;
	font-weight: 400;
	line-height: 1.5;
	margin: 0;
	text-align: left;
	color: var(--color);
	background-color: var(--body-bg-color);
	scroll-behavior: smooth;
}

@media (min-width: 576px) {
	body {
		font-size: 1.6rem;
	}
}

@media (prefers-reduced-motion: reduce) {
	body {
		scroll-behavior: auto;
	}
}

