[class*='Afb'] blockquote cite:before {
	content: '- ';
}

[class*='Figure'],
[class*='Afb'] figure {
	display: flex;
	flex-direction: column;
	margin-top: calc(calc(var(--spacer-base) * 1.5) * 2);
	margin-bottom: calc(calc(var(--spacer-base) * 1.5) * 2);
	padding: calc(var(--spacer-base) * 2.5);
	border-radius: 3px;
	background-color: #333;
	align-items: center;
	justify-content: center;
}

[class*='Figure'] figcaption,
[class*='Afb'] figure figcaption {
	margin-top: calc(var(--spacer-base) * 2.5);
	text-align: center;
	color: #d9d9d9;
}

[class*='Figure'] img,
[class*='Afb'] figure img {
	max-height: 60vh;
}

[class*='Figure'][class*='--light'],
[class*='Afb'] figure[class*='--light'] {
	background-color: #e6e6e6;
}

[class*='Figure'][class*='--light'] figcaption,
[class*='Afb'] figure[class*='--light'] figcaption {
	color: #404040;
}
